* {
    color: white;
    margin: 0;
    padding: 0;
    line-height: 1em;
    font-style: normal;
    font-variant-ligatures: normal;
    font-variant-caps: normal;
    font-variant-numeric: normal;
    font-variant-east-asian: normal;
    font-weight: 100;
    font-stretch: normal;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothingx: antialiased;
    -webkit-text-size-adjust: 100%;
    text-transform: uppercase;
    box-sizing: border-box;
}

html {
    font-size: 10px;
}

body {
    background: linear-gradient(45deg, rgba(4, 3, 8, 1) 0%, rgba(14, 11, 25, 1) 51%, rgba(4, 3, 8, 1) 100%);
    overflow-x: visible;
}


.background {
    opacity: 0.1;
    position: absolute;
    width: 100%;
    height: 200vh;
    background-image: url('./images/image.png');

    background-size: 25%;
    animation: 30s bg alternate-reverse infinite linear;


}

@keyframes bg {
    from {
        background-position: 0 0;
    }

    to {
        background-position: 300px 100px;
    }
}

span {
    font-weight: bolder;
    line-height: 1.5;
    letter-spacing: 0.4em;
}

h1 {
    font-size: 4.5rem;
}

h2 {
    font-size: 3rem;
}

h3 {
    font-size: 2rem;
}

h4 {
    font-size: 1.3rem;
}